.footer {
  background-color: #333;
  color: #fff;
  padding: 20px;
  text-align: center;
  font-family: Arial, sans-serif;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px 0;
  flex-wrap: wrap;
}

.footer a {
  color: #000000;
  text-decoration: none;
}

.footer-section {
  flex: 1;
  padding: 10px;
  min-width: 250px;
}

.footer-section h2 {
  font-size: 1.5rem;
  color: #ff9900;
  margin-bottom: 10px;
}

.footer-section p,
.footer-section ul {
  font-size: 1rem;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

.footer-section ul {
  list-style: none;
}

.footer-section ul li {
  margin: 5px 0;
}

.footer-section ul li a {
  color: #ff9900;
  text-decoration: none;
}

.footer-section ul li a:hover {
  text-decoration: underline;
}

.footer-bottom {
  padding-top: 10px;
  border-top: 1px solid #555;
}

.footer-bottom p {
  font-size: 0.9rem;
  color: #aaa;
}