.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  padding: 20px 50px;
  color: #fff;
}

.navbar-logo {
  display: flex;
  align-items: center;
  cursor: pointer; /* Indicate interaction */
}

.navbar-logo h1 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-left: 10px;
}

.navbar-logo-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.navbar-links {
  list-style-type: none;
  display: flex;
  gap: 20px;
}

.navbar-links li a {
  position: relative;
  color: #fff;
  text-decoration: none;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.navbar-links li a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -5px;
  height: 2px;
  width: 100%;
  background-color: #f0a500;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease;
}

.navbar-links li a:hover {
  color: #f0a500;
}

.navbar-links li a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}