.main-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #000;
    color: white;
}

.intro-text {
    margin-bottom: 20px;
    font-size: 2.5rem;
    text-align: center;
    color: #fff;
}

.option {
    text-align: center;
    margin: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.option-image {
    width: 300px;
    height: auto;
    border-radius: 10px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.option-image:hover {
    transform: scale(1.20);
    box-shadow: 0 10px 20px rgba(255, 255, 255, 0.2);
}

.option p {
    margin-top: 10px;
    font-size: 1.5rem;
    color: white;
}