.about-us-container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  max-width: 100%;
  overflow-x: hidden;
}

.about-header {
  text-align: center;
  padding: 60px 20px 20px;
  background: linear-gradient(135deg, #f5f7fa 0%, #e9edf2 100%);
}

.about-header h1 {
  font-size: 3.5rem;
  margin-bottom: 15px;
  color: #121212;
  font-weight: 700;
}

.about-divider {
  height: 4px;
  width: 80px;
  background: #3498db;
  margin: 0 auto 30px;
  border-radius: 2px;
}

.mission-section {
  padding: 60px 20px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mission-content {
  max-width: 900px;
  text-align: center;
}

.mission-content h2 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 30px;
  position: relative;
  display: inline-block;
}

.mission-content h2:after {
  content: '';
  position: absolute;
  width: 50%;
  height: 3px;
  background-color: #3498db;
  bottom: -10px;
  left: 25%;
}

.mission-content p {
  font-size: 1.3rem;
  line-height: 1.8;
  margin-bottom: 20px;
  color: #555;
}

.values-section {
  padding: 70px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.values-section h2 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 50px;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.value-card {
  background: white;
  border-radius: 10px;
  padding: 30px 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.value-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}

.value-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.value-card h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #2c3e50;
}

.value-card p {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.5;
}

.testimonials-section {
  padding: 70px 20px;
  background-color: #121212;
  color: white;
  text-align: center;
}

.testimonials-section h2 {
  font-size: 2.5rem;
  margin-bottom: 50px;
  color: white;
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  padding-bottom: 30px;
  height: auto;
  min-height: 600px;
}

.testimonial {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  background: #1c1c1c;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.6);
  transition: transform 0.5s ease-in-out;
  flex-direction: row;
  flex-wrap: wrap;
}

@media (max-width: 992px) {
  .testimonial {
    flex-direction: column;
    padding: 30px;
  }
  
  .testimonial-content {
    margin-left: 0;
    margin-top: 30px;
    text-align: center;
  }
}

.testimonial-image, .testimonial-video {
  width: 450px;
  height: 450px;
  border-radius: 20px;
  object-fit: cover;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.testimonial-image:hover, .testimonial-video:hover {
  transform: scale(1.02);
}

.testimonial-content {
  margin-left: 40px;
  text-align: left;
  flex: 1;
  min-width: 300px;
}

.testimonial-content h3 {
  margin: 0;
  font-size: 2rem;
  color: #3498db;
}

.testimonial-content .role {
  font-size: 1.2rem;
  color: #a5a5a5;
  margin: 10px 0 20px;
}

.testimonial-content .quote {
  font-size: 1.4rem;
  line-height: 1.6;
  font-style: italic;
  position: relative;
  padding: 0 20px;
}

.testimonial-content .quote::before,
.testimonial-content .quote::after {
  content: '"';
  font-size: 2.5rem;
  position: absolute;
  color: #3498db;
  opacity: 0.7;
}

.testimonial-content .quote::before {
  top: -15px;
  left: 0;
}

.testimonial-content .quote::after {
  bottom: -15px;
  right: 0;
}

.navigation {
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.nav-button {
  background: #3498db;
  color: white;
  border: none;
  padding: 15px 25px;
  margin: 0 20px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.2rem;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-button:hover {
  background: #2980b9;
  transform: scale(1.1);
}

.dots-container {
  display: flex;
  justify-content: center;
  margin: 0 30px;
}

.dot {
  height: 12px;
  width: 12px;
  margin: 0 5px;
  background-color: #555;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background-color: #3498db;
  transform: scale(1.3);
}

.dot:hover {
  background-color: #777;
}

.autoplay-toggle {
  margin-top: 20px;
  background: transparent;
  color: #a5a5a5;
  border: 1px solid #a5a5a5;
  padding: 8px 20px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.autoplay-toggle:hover {
  color: white;
  border-color: white;
}

.stats-section {
  padding: 70px 20px;
  background: linear-gradient(135deg, #3498db 0%, #2c3e50 100%);
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  color: white;
}

.stat-card {
  text-align: center;
  padding: 20px;
  width: 200px;
  margin: 20px;
}

.stat-card h3 {
  font-size: 3rem;
  margin-bottom: 15px;
  font-weight: 700;
}

.stat-card p {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.join-us-section {
  padding: 100px 20px;
  text-align: center;
  background-color: #f9f9f9;
}

.join-us-section h2 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 20px;
}

.join-us-section p {
  font-size: 1.3rem;
  color: #555;
  max-width: 700px;
  margin: 0 auto 40px;
  line-height: 1.6;
}

.join-button {
  background: #3498db;
  color: white;
  border: none;
  padding: 15px 40px;
  font-size: 1.2rem;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.4);
}

.join-button:hover {
  background: #2980b9;
  transform: translateY(-5px);
  box-shadow: 0 7px 20px rgba(52, 152, 219, 0.6);
}

/* Keep legacy class support for backward compatibility */
.pera {
  background-color: #00000000;
  padding: 40px 20px;
  color: #333;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}

.pera h1 {
  font-size: 2.5rem;
  color: #121212;
  margin-bottom: 15px;
}

.pera p {
  font-size: 1.2rem;
  line-height: 1.8;
  max-width: 800px;
  margin: 0 auto;
}